import React, { useState, ChangeEvent, FormEvent } from "react";
import Button from "../components/Button";
import left_arrow from "../icons/left_arrow.svg";
import Title from "../components/Title";
import { useNavigate } from "react-router";
import { useDispatch } from "react-redux";
import { alarmModalActions } from "../store/alarmModal";
import { Cookies } from "react-cookie";
import axios from "axios";
import { SERVER_URL } from "../data/url";

function NicknamePage() {
  const [nickname, setNickname] = useState<string>("");
  const [nicknameError, setNicknameError] = useState<string>("");
  const [isClicked, setIsClicked] = useState<boolean>(false);
  const [disabled, setDisabled] = useState(true);
  const dispatch = useDispatch();

  const cookies = new Cookies();

  const handleNicknameChange = (e: ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setNickname(value);
    validateNickname(value);
  };

  const validateNickname = (value: string) => {
    if (!value) {
      setNicknameError(" ");
      setDisabled(true);
    } else if (value.length < 2) {
      setNicknameError("2  ");
      setDisabled(true);
    } else if (value.length > 12) {
      setNicknameError("12  ");
      setDisabled(true);
    } else if (!/^[--a-zA-Z0-9]*$/.test(value)) {
      setNicknameError(", ,    ");
      setDisabled(true);
    } else {
      setNicknameError("  ");
      setDisabled(false);
    }
  };

  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    //   (   )
    if (nickname === "test") {
      setNicknameError("   .");
      return;
    }
    //   
    console.log(" :", nickname);
    const token = cookies.get("id");
    const userNo = cookies.get("userNo");

    axios
      .put(
        `${SERVER_URL}/users/${userNo}/nickname`,
        { nickname },
        {
          headers: { Authorization: `Bearer ${token}` },
        }
      )
      .then((response) => {});

    navigate("/home");
    dispatch(alarmModalActions.openModal());
  };

  const handleInputBlur = () => {
    setIsClicked(true);
    if (nickname === "") {
      setNicknameError(" ");
    }
  };

  const navigate = useNavigate();
  return (
    <div className="w-[375px]  bg-white pt-[76px] pb-[52px] px-4 flex flex-col justify-between">
      <form
        onSubmit={handleSubmit}
        className="h-full flex flex-col justify-between"
      >
        <div>
          <img src={left_arrow} alt="" />
          <div className="flex flex-col mt-4 mb-7">
            <Title text={""} />
            <Title text={" "} />
          </div>

          <input
            className="w-[343px] rounded-[10px] border-[0.5px] placeholder:text-[#595959] placeholder:font-medium border-[#595959] px-4 py-[17px] mb-2"
            placeholder="  "
            value={nickname}
            onChange={handleNicknameChange}
            onBlur={handleInputBlur}
          />
          {nicknameError && (
            <p className=" text-base text-[#ff3d3d] font-medium">
              {nicknameError}
            </p>
          )}
        </div>
        <Button text={""} disabled={disabled} />
      </form>
    </div>
  );
}

export default NicknamePage;
